import React, { useState } from 'react';
import axios from 'axios';

const Contact = () => {
    const [formData, setFormData] = useState({ name: '', email: '', message: '' });

    const handleSubmit = (e) => {
        e.preventDefault();
        axios.post('http://localhost:5000/api/contact', formData)
            .then(() => alert('Message sent successfully!'))
            .catch((err) => console.error(err));
    };

    return (
        <div style={{
            textAlign: 'center',
            padding: '4rem 2rem'
        }}>
            <h1>Contact Me</h1>
            <form onSubmit={handleSubmit} style={{
                maxWidth: '600px',
                margin: '0 auto',
                display: 'flex',
                flexDirection: 'column',
                gap: '1rem'
            }}>
                <input type="text" placeholder="Your Name" value={formData.name} 
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    required
                    className="neon-border"
                    style={{ padding: '0.5rem', background: 'transparent', color: '#fff' }} />
                <input type="email" placeholder="Your Email" value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    required
                    className="neon-border"
                    style={{ padding: '0.5rem', background: 'transparent', color: '#fff' }} />
                <textarea placeholder="Your Message" value={formData.message}
                    onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                    required
                    className="neon-border"
                    style={{ padding: '0.5rem', background: 'transparent', color: '#fff', height: '100px' }} />
                <button type="submit" style={{
                    background: '#58a6ff',
                    color: '#fff',
                    border: 'none',
                    padding: '0.75rem 1.5rem',
                    borderRadius: '5px',
                    cursor: 'pointer',
                    transition: 'background 0.3s ease'
                }}>Send</button>
            </form>
        </div>
    );
};

export default Contact;
